import { Component, For, JSX, Show, createMemo, createSignal } from 'solid-js';
import { FormSection, FormTemplate, FormLineTemplate, FormNumberTemplate, Label, ExtendedLabel } from '../core/FormTemplate';
import { template } from 'solid-js/web';
// import { navigateToPage } from '../App';
// import { twMerge } from 'tailwind-merge';

interface ILabelProps {
  //   class?: string;
  //   startIcon?: any;
  //   onClick?: (event: MouseEvent) => void;
  //   disabled?: boolean;
  //   children: JSX.Element;
  label: Label
  error?: string;
  children: JSX.Element;
}

const LabelComponent: Component<ILabelProps> = (props) => {
  const extendedTemplate = createMemo<ExtendedLabel>(() => {
    //export type Label = string | { label?: string, required?: string, help?: string, placeHolder?: string }

    if (!props.label || typeof props.label === 'string') {
      return { 'label': props.label as string }
    } else {
      return props.label
    }
  });
  return (
    <label class="form-control w-full max-w-xs">
      <div class="label">
        <span class="label-text">{extendedTemplate().label}</span>
        <span class="label-text-alt">{extendedTemplate().required}</span>
      </div>
      {props.children}
      <div class="label">
        <span class="label-text-alt">{props.error ?? extendedTemplate().help}</span>
      </div>
    </label>
  );
};

export default LabelComponent;